import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ContractApiService } from 'src/app/core/services/contact.services/contract.api.service';
import { JsonService } from 'src/app/core/services/json.services/json.service';
import { TransactionFormComponent } from '../transaction.form.component copy/transaction.form.component';

@Component({
  selector: 'app-dynamic-form',
  templateUrl: './dynamic.form.component.html',
  styleUrls: ['./dynamic.form.component.scss']
})
export class DynamicFormComponent implements OnInit {
  t;
  constructor(public dialog: MatDialog, private json: JsonService, public contract: ContractApiService) {
  }
  private diaTrans(after: any): void {
    this.dialog.open(TransactionFormComponent, {
      width: '250px',
    }).afterClosed().subscribe(after);
  }
  ngOnInit(): void {
    this.clearMsig();
  }
  private diaJson(after: any): void {
    this.contract.getFromJsonTemplate().subscribe(after);
  }
  public clearMsig(): void {
    this.t = {
      accInfo: {
        chain: '',
        name: this.contract.getUserName(),
        account: '',
        expiration: ''
      },
      trans: [
      ],
      auths: [
      ],
      topAuths: [

      ],
      producersConst: 'collect',
      producers: []
    };
    if (this.contract.getUserName() && this.contract.getUserName() !== '') {
      this.addTran();
    }
  }
  public async import() {
    this.json.importObjectFromJson((e) => {
      const reader = new FileReader();
      reader.onload = (e2 => {
        const obj = JSON.parse(e2.target.result.toString());
        if (this.t.accInfo.chain != obj.t.accInfo.chain) {
          alert(`First you need to change the chain to ${obj.t.accInfo.chain}`);
        }
        else if (this.t.accInfo.chain != obj.t.accInfo.chain) {
          alert(`First you need to log in as ${obj.t.accInfo.name}`);
        }
        else {
          this.t = obj.t;
        }
      });
      reader.readAsText(e.target.files[0]);
    });
  }
  public export() {
    this.json.exportObjectAsJson(
      {
        t: this.t,
        a: this.contract.getApiIndex().split(':: ')[1]
      }
      , 'msig-save-' + new Date().toLocaleString());
  }
  public addTran() {
    this.diaTrans((tran) => {
      if (!tran) {
        return;
      }
      else {
        this.diaJson((jsonArray: any[]) => {
          if (!jsonArray || !jsonArray.find(s => s.name === tran.action.name)) {
            this.t.trans.push({
              name: tran.name,
              chain: this.contract.getApiIndex(),
              action: {
                name: tran.action.name,
                fields: tran.action.fields,
                _fields: tran.action._fields
              },
              auths: [
                {
                  _name: '',
                  _perm: '',
                  perms: []
                }
              ],
              topAuths: [

              ]
            });
          }
          else {
            this.t.trans.push({
              name: tran.name,
              chain: this.contract.getApiIndex(),
              action: {
                name: tran.action.name,
                fields: tran.action.fields,
                _fields: tran.action._fields
              },
              auths: [
                {
                  _name: '',
                  _perm: '',
                  perms: []
                }
              ]
            });
          }
        });
      }
      this.t.accInfo.account = this.contract.getUserName();
    });
  }
  public addLocalAuth(tId: number): void {
    if (this.t.trans[tId].auths.length !== this.t.trans[tId].action._fields.length)
      this.t.trans[tId].auths.push(
        {
          _name: '',
          _perm: '',
          perms: []
        }
      );
  }
  public removeLocalAuth(tId: number, i: number): void {
    const globalIndex = this.t.auths.indexOf(this.t.auths.find((auth) => auth._name === this.t.trans[tId].auths[i]._name));
    if (globalIndex >= 0) {
      this.t.auths = this.t.auths.filter((x, y) => y !== globalIndex);
    }
    this.t.trans[tId].auths = this.t.trans[tId].auths.filter((x, y) => y !== i);
  }
  public clearLocalAuth(value: string, tId: number, i: number): void {
    const globalIndex = this.t.auths.indexOf(this.t.auths.find((auth) => auth._name === value));
    if (globalIndex >= 0) {
      this.t.auths = this.t.auths.filter((x, y) => y !== globalIndex);
    }
  }
  public removeLocalTransaction(tId: number) {
    this.t.trans = this.t.trans.filter((x, y) => y !== tId);
    this.t.auths = this.t.auths.filter((x) => x._id !== tId);
  }
  public getIndexByFieldName(from: string, id: number): number {
    if (from === 'top' || from === 'committe') {
      return 0;
    }
    const index = this.t.trans[id].auths.find((auth) => auth._name === from).perms.indexOf(this.t.trans[id].auths.find((auth) => auth._name === from)._perm);
    this.t.trans[id].auths.find((auth) => auth._name === from).permIndex = index
    return index;
  }
  public collectTopProducers(): void {
    this.t.producersConst = 'top';
    this.t.auths = [];
    this.t.producers = [];
    this.t.producersKeys = [];
    this.contract.getTopProducers().subscribe((producers) => {
      producers.rows.filter(row => row.is_active === 1).forEach((producer, i) => {
        if (i < 30) {
          this.t.producersKeys.push(producer.producer_key);
          this.t.producers.push(producer.owner);
        }
      });
    });
  }
  public collectProtonProducers(): void {
    this.t.producersConst = 'proton';
    this.t.auths = [];
    this.t.producers = [];
    this.t.producersKeys = [];
    this.contract.getProducers('admin.proton').subscribe((accInfo) => {
      const perms = [];
      accInfo.permissions.forEach((permission) => {
        if (permission.perm === 'committee') {
          perms.push({
            name: permission.perm,
            accounts: permission.auth.accounts.map((account) => ({ acc: account.actor, prm: account.permission })),
            keys: permission.auth.keys
          });
        }
      });
      this.t.auths.push({
        _name: 'committe',
        _id: 0,
        _value: 'admin.proton',
        permIndex: 0,
        perms
      });
    });
  }
  public collectAllProducers(): void {
    this.t.producersConst = 'collect';
    this.t.auths = [];
    this.t.producers = [];
    this.t.producersKeys = [];
    this.t.trans.forEach((tran, tranIndex) => {
      tran.action.fields.forEach((from) => {
        if (tran.auths.find((auth) => auth._name === from.name)) {
          this.contract.getProducers(from.value).subscribe((accInfo) => {
            const perms = [];
            const _perms = [];
            accInfo.permissions.forEach((permission) => {
              perms.push({
                name: permission.perm,
                accounts: permission.auth.accounts.map((account) => ({ acc: account.actor, prm: account.permission })),
                keys: permission.auth.keys
              });
              _perms.push(permission.perm);
            });
            tran.auths.find((auth) => auth._name === from.name).perms = _perms;
            tran.auths.find((auth) => auth._name === from.name)._perm = _perms[0];
            if (!this.t.auths.find((auth) => auth._name === from.name && auth._id === tranIndex)) {
              this.t.auths.push({
                _name: from.name,
                _id: tranIndex,
                _value: from.value,
                permIndex: 0,
                perms
              });
            }
            else {
              this.t.auths.find((auth) => auth._name === from.name && auth._id === tranIndex)._value = from.value;
              this.t.auths.find((auth) => auth._name === from.name && auth._id === tranIndex).perms = perms;
              this.t.auths.find((auth) => auth._name === from.name && auth._id === tranIndex).permIndex = 0;
            }
          });
        }
      })
    });
  }
  public onFileSelected(event: any, value: any) {
    if (event.target.files.length > 0) {
      value = JSON.parse(event.target.files[0]);
    }
  }
}
