  public async deployWaxContract(t: any) {
    const pubKeys = [];
    t.auths.forEach((a) => {
      a.perms[a.permIndex].keys.forEach(k => {
        pubKeys.push(k);
      })
    })
    pubKeys.push(this.publicKey);
    const wax = new WaxJS({
      rpcEndpoint: 'https://wax.greymass.com',
      userAccount: t.accInfo.name,
      pubKeys: pubKeys
    });

    const result = await wax.api.transact(
      {
        actions: this.parseActions(t.trans, t.auths, wax)
      }, {
      blocksBehind: 3,
      expireSeconds: 1200,
    });
  }
