  private parseActions(transactions: any[], authorizations: any[], api?: any): any[] {
    const actions = [];
    const abiDefinition = api.abiTypes?.get(`abi_def`);
    transactions.forEach((tran, tranIndex) => {
      let authorization = [];
      if (authorizations.length > 0) {
        authorization = authorizations.find((auth) => auth._id === tranIndex).perms?.accounts?.map(acc => { return { actor: acc.acc, permission: acc.prm } }) ?? [];
      }
      tran.action.fields.forEach((from) => {
        if (tran.auths.find((auth) => auth._name === from.name)) {
          authorization.push({
            actor: tran.auths.find((auth) => auth._name === from.name)._value,
            permission: tran.auths.find((auth) => auth._name === from.name)
              .perms[tran.auths.find((auth) => auth._name === from.name).permIndex]._perm
          });
        }
      });
      let data = {};
      tran.action.fields.forEach(field => {
        if(field.name === 'bytes'){
          const buffer = new Serialize.SerialBuffer({
            textEncoder: new TextEncoder(),
            textDecoder: new TextDecoder(),
          })
          let abi = abiDefinition.fields.reduce(
            (acc, { name: fieldName }) =>
              Object.assign(acc, { [fieldName]: acc[fieldName] || [] }),
            field.value
          )
          abiDefinition.serialize(buffer, abi)
          field.value = Buffer.from(buffer.asUint8Array()).toString(`hex`);
        }
        else{
          data[field.name] = field.value;
        }
      })
      actions.push({
        account: tran.name,
        name: tran.action.name,
        authorization,
        data,
      });
    });
    return actions;
  }
